            <span>备&nbsp&nbsp&nbsp注</span>
            <span
              title="点击修改备注"
              style="cursor:pointer"
              @click="changeRemark(info, selectedChat.user.name)"
            >{{ selectedChat.user.name }}
            </span>
          </div> -->
