<script>
import { fetchInfo, withdraw, setRemarks, sendCard, saveStore, textMsgs, addChat,friendAdd } from '@/api'
import { mapGetters, mapState, mapActions } from 'vuex'
import selectUser from '@/components/selectUser'
import groupInfo from '@/components/group/groupInfo.vue'
import lookMapInfo from "@/components/lookMapInfo.vue";

// 高德地图
import AmapVue from '@amap/amap-vue';

const { photoUrl, chatUrl } = window.__gconf
export default { 
  components: {
    selectUser,
	  groupInfo,
    lookMapInfo
  },
  filters: {
    // 将日期过滤为 hour:minutes
    time (timestamp) {
      timestamp = new Date(timestamp).valueOf() / 1000
      let curTimestamp = parseInt(new Date().getTime() / 1000), //当前时间戳
        timestampDiff = curTimestamp - timestamp, // 参数时间戳与当前时间戳相差秒数
        curDate = new Date(curTimestamp * 1000), // 当前时间日期对象
        tmDate = new Date(timestamp * 1000), // 参数时间戳转换成的日期对象
        Y = tmDate.getFullYear(),
        m = tmDate.getMonth() + 1, d = tmDate.getDate(),
        H = tmDate.getHours(), i = tmDate.getMinutes(),
        s = tmDate.getSeconds();
      if (timestampDiff < 60) { // 一分钟以内
        return '刚刚';
      } else if (timestampDiff < 3600) { // 一小时前之内
        return Math.floor(timestampDiff / 60) + '分钟前';
      } else if (curDate.getFullYear() == Y && curDate.getMonth() + 1 == m && curDate.getDate() == d) {
        return '今天 ' + ((String(H).length == 1 ? '0' : '') + H) + ':' + ((String(i).length == 1 ? '0' : '') + i);
      } else {
        var newDate = new Date((curTimestamp - 86400) * 1000); // 参数中的时间戳加一天转换成的日期对象
        if (newDate.getFullYear() == Y && newDate.getMonth() + 1 == m && newDate.getDate() == d) {
          return '昨天 ' + ((String(H).length == 1 ? '0' : '') + H) + ':' + ((String(i).length == 1 ? '0' : '') + i);
        } else if (curDate.getFullYear() == Y) {
          return ((String(m).length == 1 ? '0' : '') + m) + '月' + ((String(d).length == 1 ? '0' : '') + d) + '日 ' + ((String(H).length == 1 ? '0' : '') + H) + ':' + ((String(i).length == 1 ? '0' : '') + i);
        } else {
          return Y + '年' + ((String(m).length == 1 ? '0' : '') + m) + '月' + ((String(d).length == 1 ? '0' : '') + d) + '日 ' + ((String(H).length == 1 ? '0' : '') + H) + ':' + ((String(i).length == 1 ? '0' : '') + i);
        }
      }
    }
  },
  data () {
    return {
      photoUrl: photoUrl,
      chatUrl: chatUrl,
      showTip: false,
      info: null,
      menuVisible: false,
      currentRow: {},
      load: false,
      isFirst: false,
      defaultList: [],
      showInfo:false,
      shareItem: {},
      // 经纬度信息
				mapInfo: "",
      // 是否查看信息地图
				showMapInfo: false,
    }
  },
  computed: {
    ...mapGetters([
      'selectedChat', 
      'messages'
    ]),
    ...mapState([
      'user',
      'emojis',
      'groupChat',
      'selectId',
	  'groupDetail'
    ])
  },
  watch: {
    // 发送信息后,让信息滚动到最下面
    'selectedChat.messages': {
      handler: function (a) {
        if (!this.isFirst) {
          this.load = false
        }
        this.$nextTick(() => {
          setTimeout(() => {
            this.$refs.list ? this.$refs.list.scrollTop = this.$refs.list.scrollHeight : ''

            this.load = true
            this.isFirst = true
          }, 0)
        })
      },
      deep: true
    },
    'selectedChat.chat_id': {
      handler: function (a) {
        this.showTip = true
      }
    }
  },
  mounted () {
	
    //  在页面加载时让信息滚动到最下面
    if (this.$refs.list)
      setTimeout(() => {
        this.$refs.list.scrollTop = this.$refs.list.scrollHeight
      }, 0)
  },
  destroyed () {
    this.$store.dispatch('clearIntervalTimer')
  },
  methods: {
    ...mapActions([
      'selectFriend2',
      'fetchCharList'
    ]),
    cardInfo(item){
      console.log(JSON.stringify(item))
      fetchInfo({
        user_id: item.content.user_id,
        in: 0
      }).then(res => {
        this.info = {
          ...res.data
        }
        this.showInfo = true;
      })
    },
    viewPosition(item){
      var lat = item.content.position.lat;
      var lon = item.content.position.lon;
      this.mapInfo = lon + "," + lat;
			this.showMapInfo = true;
    },
    changeRemark (item, remark) {
      this.$prompt('请输入备注信息', '', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        inputPattern: /\S/,
        inputValue: remark,
        inputErrorMessage: '备注信息不能为空'
      }).then(({ value }) => {
        setRemarks({
          content: value,
          user_id: item.user_id
        }).then(res => {
          this.fetchCharList(this.selectId || '')
          // this.showInfoBanner(item)
          // this.fetchFriendList()
        })
      })
    },
    handleInvite () {
      this.$set(this, 'defaultList', [
        ...this.selectedChat.groupImgs.map(item => item.user_id)
      ])
	  console.log(this.defaultList);
      this.$refs.selectInvite.open(this.defaultList)
    },
	handleGroup(){
		this.$refs.groupInfo.open()
	},
    showInfoBanner (item) {
      fetchInfo({
        user_id: item.userId,
        in: 0
      }).then(res => {
        this.info = {
          ...res.data
        }
      })
    },
    add (item) {
      this.$prompt('你需要发送验证申请,等对方通过', '朋友验证', {
        confirmButtonText: '确定',
        cancelButtonText: '取消'
      }).then(({ value }) => {
        friendAdd({
          user_id: item.user_id,
          is_type: 0,
          content: value
        }).then(res => {
          this.$message.success('申请成功')
          this.showInfo = false
        })
      })
    },
    handleInvited (data) {
      const users = []
      data.forEach(item => {
        if (!this.defaultList.includes(item.user_id)) {
          users.push(item.user_id)
        }
      })
      addChat({
        users: JSON.stringify(users),
        list_id: this.selectedChat.list_id
      }).then(res => {
        this.fetchCharList(this.selectId || '')
      })
    },
    handleOk (data) {
      console.log(data)
      textMsgs({
        list_ids: data
          .filter((item) => item.list_id)
          .map((item) => {
            return item.list_id;
          })
          .join(),
        content_type: this.currentRow.msgItem_type,
        current_list_id: this.selectedChat.list_id,
        content: JSON.stringify(this.currentRow.content)
      }).then((res) => {
        this.fetchCharList(this.selectId || '');
      });
    },
    showHide () {
      setTimeout(() => {
        this.info = null
      }, 300)
    },
    handleOption (event, item) {
      this.menuVisible = false // 先把模态框关死，目的是 第二次或者第n次右键鼠标的时候 它默认的是true
      this.menuVisible = true // 显示模态窗口，跳出自定义菜单栏
      event.preventDefault() //关闭浏览器右键默认事件
      this.currentRow = {
        ...item
      }
      console.log(item)
      var menu = document.querySelector('.contextmenu-msg_menu')
      this.styleMenu(menu, event)
    },
    foo () {
      // 取消鼠标监听事件 菜单栏
      this.menuVisible = false
      document.removeEventListener('click', this.foo) // 关掉监听，
    },
    styleMenu (menu, event) {
      if (event.clientX > 1800) {
        menu.style.left = event.clientX - 100 + 'px'
      } else {
        menu.style.left = event.clientX + 1 + 'px'
      }
      document.addEventListener('click', this.foo) // 给整个document新增监听鼠标事件，点击任何位置执行foo方法
      if (event.clientY > 700) {
        menu.style.top = event.clientY - 30 + 'px'
      } else {
        menu.style.top = event.clientY - 10 + 'px'
      }
    },
    handleShare (item) {
      this.shareItem = item
      this.$refs.handleShare.open()
    },
    handleShared (data) {
      // alert(JSON.stringify(data))
      sendCard({
        users: JSON.stringify([this.shareItem.userId + '']),
        list_id: this.selectedChat.list_id
      }).then(res => {
        this.fetchCharList(this.selectId || '')
        this.$message.success('发送成功')
      })
    },
    handleSetting (type) {
      const data = {
        list_id: this.currentRow.list_id,
        type: this.currentRow.msg_type,
        msg_id: this.currentRow.msg_id
      }
      let fetchUrl
      if (type == 2) {
        fetchUrl = saveStore
      } else if (type == 1) {
        fetchUrl = withdraw
      } else {
        this.$refs.selectUser.open()
        return
        // fetchUrl = deleteChat
      }
      fetchUrl(data).then(res => {
        if (type == 1) {
          this.$store.dispatch('selectSession', this.selectId)
        } else if (type == 2) {
          if (res.msg == '收藏成功') {
            this.$message.success('收藏成功')
          } else {
            this.$message.warning(res.msg)
          }
        }
      })
    },
    //  在发送信息之后，将输入的内容中属于表情的部分替换成emoji图片标签
    //  再经过v-html 渲染成真正的图片
    replaceFace (con) {
      if (con && con.includes('[') && con.includes(']')) {
        var emojis = this.emojis;
        var newCon = ''
        con.split(']').forEach(item => {
          if (item.lastIndexOf('[') > 0) {
            item.split('[').forEach(a => {
              if (a != '') {
                for (var i = 0; i < emojis.length; i++) {
                  if (emojis[i].code.includes(a)) {
                    newCon += '<img src="static/emoji/' + emojis[i].file + '"  alt="" style="vertical-align: middle; width: 24px; height: 24px" />';
                    return
                  } else if (i + 1 == emojis.length) {
                    newCon += a
                  }
                }
              } else {
                newCon += '['
              }
            })
          }
          if (item != '') {
            for (var i = 0; i < emojis.length; i++) {
              if (emojis[i].code.includes(item)) {
                newCon += '<img src="static/emoji/' + emojis[i].file + '"  alt="" style="vertical-align: middle; width: 24px; height: 24px" />';
                return
              }
            }
          }
        })

        return newCon;
      }
      return con;
    }
  }
}
</script>

